import { VStack, Text } from "@chakra-ui/react";
import MotifBySize from "./MotifBySize";
import { Stitch, StitchList } from "@tanyoknits/shared";
import { MotifBySizeItem } from "../utils/types";

interface Props {
  intro: string;
  motifBySize: MotifBySizeItem[];
  stitches: Stitch[];
}
export default function StitchPattern({ intro, motifBySize, stitches }: Props) {
  return (
    <VStack spacing={8} align="left">
      <Text>{intro}</Text>
      <MotifBySize
        motifBySize={motifBySize.map((m) => {
          const { motif, sizes } = m;
          return {
            size: sizes.join(", "),
            motif: { ...motif, title: undefined },
          };
        })}
      />
      <StitchList stitches={stitches} />
    </VStack>
  );
}
